import debounce from 'lodash.debounce';
import config from '@/config';
import Button from '@/components/Button';
import { post } from '@/api/withdraw';
import {
    addCalendarEvent,
    setSwipeBackStatus,
    openCommonWebViewByPath,
    toast,
    getReadTime,
} from '@/utils/jsApiV2/client';
import { goTab } from '@/utils/jsApiV2/deeplink';
import { get } from '@/utils/cache';
import WithdrawOptions from './WithdrawOptions';
import { cacheUserLevelKey, pageType } from './config';

export default {
    components: {
        Button,
        WithdrawOptions,
    },
    data() {
        return {
            selectedCash: null,
            phonenumber: '',
            typeMap: {
                wechat: '微信',
                phonenumber: '话费',
            },
            loading: false,
            readTime: parseInt(getReadTime() / 60000, 10),
            pageType: this.$route.query.channel || get(pageType) || 'NORMAL',
        };
    },
    computed: {
        type() {
            return this.selectedCash && this.selectedCash.type;
        },
        withdrawOptions() {
            return this.withdrawInfo.withdrawOptions || [];
        },
    },
    props: {
        isAuth: {
            type: Boolean,
            default: false,
        },
        authPhonenumber: {
            type: String,
            default: '',
        },
        withdrawInfo: {
            type: Object,
            default: () => ({}),
        },
    },
    methods: {
        withdrawHandler: debounce(function handler(onSuccess, onError) {
            if (!this.selectedCash) return;

            this.withdraw({
                type: this.selectedCash.type,
                cash: this.selectedCash.cash,
                pageType: this.pageType,
                ...(this.phonenumber && { phonenumber: this.phonenumber }),
            }, onSuccess, onError);

            // 当点击话费输入框中的立即提现按钮时不上报
            if (!this.phonenumber) {
                if (this.pageType === 'CHANNEL_NEW_USER') {
                    this.$o2o({
                        e_c: '裂变新手链路提现',
                        e_a: '点击',
                        e_n: '立即兑换',
                        e_x: {
                            task_day: this.$route.query.taskday || get('taskday') || '',
                            status: this.withdrawInfo.coin === 0 ? '无余额' : '有余额',
                        },
                    });
                } else {
                    this.$o2o({
                        e_c: '提现',
                        e_a: '点击',
                        e_n: '立即提现',
                        e_x: {
                            string_type: `${this.typeMap[this.selectedCash.type]}${this.selectedCash.cash / 100}元`,
                            status: '立即提现',
                        },
                    });
                }
            }
        }, config.debounceWait),
        withdraw(params, onSuccess, onError) {
            const type = params.type;
            const typeName = this.typeMap[type];

            if (window.miVerify.register === false) {
                toast('网络异常，请退出后重试');
                return;
            }

            if (!window.miVerify.withdraw.verify) {
                toast('网络延迟，请稍后重试');
                return;
            }
            // 待添加验证步骤
            window.miVerify.withdraw.onSuccessRequest = (token) => {
                this.loading = true;
                setSwipeBackStatus('true');
                this.$o2o({ e_c: '滑动验证码', e_a: '成功', e_n: '滑动验证码解锁成功', e_x: { trigger_source: `${typeName}提现` } });
                post({
                    ...params,
                    ...token,
                })
                    .then((res) => {
                        if (typeof onSuccess === 'function') {
                            onSuccess(res);
                        }

                        this.loading = false;
                    })
                    .catch((res) => {
                        if (res && res.status === '1001') {
                            this.$o2o({
                                e_c: '滑动验证码',
                                e_a: '失败',
                                e_n: '滑动验证码解锁失败',
                                e_x: { trigger_source: `${typeName}提现` },
                            });
                        }

                        this.$o2o({
                            e_c: '提现',
                            e_a: '失败',
                            e_n: '提现失败',
                            e_x: { type: typeName },
                        });

                        if (typeof onError === 'function') {
                            onError(res);
                        }

                        this.loading = false;
                    });
            };
            window.miVerify.withdraw.onCloseRequest = (code) => {
                setSwipeBackStatus('true');
                switch (code) {
                case 0:
                    this.$o2o({ e_c: '滑动验证码', e_a: '成功', e_n: '滑动验证码解锁成功', e_x: { trigger_source: `${typeName}提现` } }); break;
                case 1:
                    this.$o2o({ e_c: '滑动验证码', e_a: '关闭', e_n: '滑动验证码手动关闭', e_x: { trigger_source: `${typeName}提现` } }); break;
                case 2:
                    this.$o2o({ e_c: '滑动验证码', e_a: 'eventId 过期', e_n: '滑动验证码 eventId 过期', e_x: { trigger_source: `${typeName}提现` } }); break;
                case 3:
                    this.$o2o({ e_c: '滑动验证码', e_a: '系统错误', e_n: '滑动验证码系统错误', e_x: { trigger_source: `${typeName}提现` } }); break;
                default:
                    break;
                }
            };
            const userLevel = this.withdrawInfo.userLevel !== undefined ?
                this.withdrawInfo.userLevel : Number(get(cacheUserLevelKey));
            if (userLevel === 0) {
                setSwipeBackStatus('false');
                window.miVerify.withdraw.verify.start({
                    activeVerify: 'false',
                });
            } else if (userLevel === 1) {
                setSwipeBackStatus('false');
                window.miVerify.withdraw.verify.start({
                    activeVerify: 'true',
                });
            } else if (userLevel === 2) {
                if (config.versionCode < 40850) {
                    toast('账号异常、升级新版APP可申诉');
                } else {
                    this.showWithdrawApplyDialog = true;
                }
            }
        },
        commitWithdraw(res) {
            const withdraw = this.selectedCash.cash / 100;
            const nextDriblet = res.nextDriblet / 100;
            let cash;
            let modalText;

            switch (withdraw) {
            case 0.3:
                cash = 1;
                break;
            case 1:
                cash = 5;
                break;
            default:
            }

            // 提现成功后全部跳转到赚金币tab，但是某些低版本可能不支持 80ban
            if (config.versionCode >= 40880) {
                // 对照组和实验组采用不同的方案
                if (this.pageType === 'CHANNEL_NEW_USER' || nextDriblet === 0) {
                    modalText = '';
                } else if (nextDriblet === 5) {
                    modalText = `连续签到3天，再提${nextDriblet}元`;
                } else {
                    modalText = `明天来，再提${nextDriblet}元`;
                }
                goTab(`chatTab?text=${modalText}&money=${withdraw}`);
            } else {
                openCommonWebViewByPath(`/mobile-v2/withdraw/progress?withdraw=${withdraw}${cash ? `&cash=${cash}` : ''}${res.nextDriblet > 0 ? `&nextDriblet=${res.nextDriblet}` : ''}`);
            }
        },
        goProgress() {
            const o2oObj = {
                e_c: '提现',
                e_a: '点击',
                e_n: '提现进度按钮点击',
            };
            if (this.pageType === 'CHANNEL_NEW_USER') {
                o2oObj.e_c = '裂变新手链路提现';
                o2oObj.e_x = {
                    task_day: this.$route.query.taskday,
                };
            }
            this.$o2o(o2oObj);

            openCommonWebViewByPath('/mobile-v2/withdraw/progress');
        },
        hasCash(cash) {
            for (let i = 0; i < this.withdrawOptions.length; i += 1) {
                if (this.withdrawOptions[i].cash === cash
                    && this.withdrawOptions[i].canClick) {
                    return true;
                }
            }

            return false;
        },
        handleAddCalendar(title = '【想看】今天可提现1元零钱了，点击提现>>', description = '想看1元提现', mode = 'FREQ=DAILY;INTERVAL=1;COUNT=1', offsetDay = 1) {
            const target = new Date();

            target.setDate(target.getDate() + offsetDay);
            target.setHours(7);
            target.setMinutes(0);
            target.setSeconds(0);

            addCalendarEvent({
                title,
                description,
                startTime: target.getTime(),
                endTime: target.getTime() + 600000,
                location: 'http://www.xiangkannews.cn/a?dLHost=goto&dLPath=newsTab&start_source=calendar',
                mode,
            });
        },
    },
};
